<template>
  <div class="container">
    <div class="text-center mb-2">
      <img alt="Vue logo" src="../assets/logo.png">
    </div>
    <h2 class="text-center mb-4">todo </h2>
    <input type="text" class="form-control" v-model="userInput" @keyup.enter="addTodo" placeholder="  " ref="userInput">
    <!-- @keyup.enter     -->
    <div class="list-group mb-4"> <!-- mb-4    . -->
      <template v-for="(data, index) in activeTodoList">
        <Todo2List 
          :label="data.label" 
          @toggle-todo="toggleTodo(data)" 
          :key="index" 
        />
        <!-- data.label  label    -->
        <!--  toggle-todo     toggleTodo  . -->
      </template>
    </div>    
    <div class="text-right mb-4">
      <!--
      <div class="btn-group">
        <button type="button" class="btn btn-primary btn-sm" @click="changeState('active')"></button>
        <button type="button" class="btn btn-primary btn-sm" @click="changeState('done')"></button>
        <button type="button" class="btn btn-primary btn-sm" @click="changeState('all')"></button>
      </div>
      -->
      <b-form-radio-group v-model="selected">
        <b-form-radio value="active"></b-form-radio>
        <b-form-radio value="done"></b-form-radio>
        <b-form-radio value="all"></b-form-radio>
      </b-form-radio-group>
    </div>
    {{ todoList }}
    <br>
    {{ selected }}
  </div> 
</template>
<script>
import Todo2List from '@/components/Todo2List.vue';

export default {
  //
  data() {
      return {
        selected: "active",
        userInput: "",
        todoList: [],
        currentState: "active",
      };
  },
  computed: {
    activeTodoList() {
      return this.todoList.filter(data => this.currentState === "all" || data.state === this.currentState);
    },
  },
  watch: {
    selected() {
      this.currentState = this.selected;
    },
  },
  methods: {
    changeState(state) {
      this.currentState = state;
    },
    addTodo() {
      if (this.userInput == "") {
        this.$refs.userInput.focus();
      } else {
        this.todoList.push({
          label: this.userInput,
          state: "active",
        });
        this.userInput = "";
      }
    },
    toggleTodo(data) {
      console.log(data.label);
      data.state = data.state === "active" ? "done" : "active";
    },
  },
  components: {
    Todo2List,
  },
}
</script>
