import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import DayProfit from '../views/DayProfit.vue'
import Todo from '../views/Todo.vue'
import Todo2 from '../views/Todo2.vue'
import Login from '../views/Login.vue'
import Login2 from '../views/Login2.vue'
import NotFound from '../views/NotFound.vue'
import axios from 'axios';
import store from '../store/index.js'

Vue.use(VueRouter)

//  
const requireAuth = () => (to, from, next) => {  
  axios.get("/api/login/session/check", {
    params: {}
  }).then((response) => {        
    console.log("/api/login/session/check : ", response.data);
    let loginSuccess = response.data.success;
    //
    if(loginSuccess == true) {
        return next();//    .
    //
    } else {
      //sessionStorage.clear();
      console.log("window.location.pathname : ", window.location.pathname);
      console.log("from.fullPath : ", from.fullPath);
      console.log("to.fullPath : ", to.fullPath);
      sessionStorage.setItem("refer", to.fullPath);
      //window.scrollTo(0,0);//             . 2021-05-25
      return next('/login');
    }  
  }).catch((error) => {
    console.log(error);
    alert(error);
  });
};

const go = () => (to, from, next) => {
  console.log("zzzzzzzzzzzzzzzzzzzz");
  console.log("from.fullPath : ", from.fullPath);
  console.log("to.fullPath : ", to.fullPath);
  console.log("next : ", next);
};

//  
const requireAuth2 = () => (to, from, next) => {
  if (store.state.token != "") {
    console.log("token : ", store.state.token);  
    axios.get("/api/login/token/check", {
      headers: {
        //"x-access-token": store.state.token
      },
      params: { token: store.state.token }
    }).then((response) => {        
      console.log("/api/login/token/check : ", response.data);
      let loginSuccess = response.data.success;
      //
      if(loginSuccess == true) {
          return next();//    .
      //
      } else if(loginSuccess == false) {
        store.commit('logoutToken'); // token        
        //sessionStorage.clear();
        console.log("window.location.pathname", window.location.pathname);
        console.log("from.fullPath : ", from.fullPath);
        console.log("to.fullPath : ", to.fullPath);
        sessionStorage.setItem("refer", to.fullPath);
        //window.scrollTo(0,0);//             . 2021-05-25
        return next('/login2');
      }  
    }).catch((error) => {
      console.log(error);
      alert(error);
    });
  //token    
  } else {
    sessionStorage.setItem("refer", to.fullPath);
    window.scrollTo(0,0);//             . 2021-05-25
    return next('/login2');
  }
};

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home // import  Home  .
  },
  {
    path: '/auto/dayprofit',
    name: 'DayProfit',
    component: DayProfit
  },
  {
    path: '/todo',
    name: 'Todo',
    component: Todo,
    afterEnter: go(), //  
  },
  {
    path: '/todo2',
    name: 'Todo2',
    component: Todo2,
  },
  {
    path: '/board',
    name: 'Board',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/Board.vue'),
    beforeEnter: requireAuth(), //  
  },
  {
    path: '/upbit',
    name: 'Upbit',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/Upbit.vue'),

    //            .
    //               .   
    //   beforeEnter   .  
    beforeEnter: requireAuth2(), //  
  },
  {
    path: '/login',
    name: 'Login',
    component: Login,
  },
  {
    path: '/login2',
    name: 'Login2',
    component: Login2,
  },
  {
    path: '*',
    component: NotFound
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes,
  //        ,      
  // scrollBehavior    .
  //scrollBehavior (to, from, savedPosition) {
  scrollBehavior () {
      return { x: 0, y: 0 }
  },
})

