import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios';
import router from "../router"

//vuex store      
import createPersistedState from 'vuex-persistedstate';

//    Vuex
Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    // 
    id: "",
    count: 0,
    host: "http://127.0.0.1",
    token: "",    
  },
  //Vuex  ,  state      
  mutations: {
    increment (state) {
      state.count++
    },    
    loginToken(state, payload) {
      state.token = payload;
    },
    logoutToken(state) {
      state.token = "";
      //router.push("/upbit");
      //           . 
      //NavigationDuplicated: Avoided redundant navigation to current location
      //         .

      //      .   .
      //       .
      //router.go(router.currentRoute);

      router.push("/login2");
    },
    loginId (state, payload) {
      state.id = payload;
    },    
    logoutId (state) {
      state.id = "";
    },    
    logoutSession (state) {
      console.log("state",state);
      axios.get("/api/logout/session", {
        headers: {},
        params: {}
      }).then((response) => {        
        console.log("/api/logout/session : ", response.data);
        state.id = "";
        router.push("/"); // 
      }).catch((error) => {
        console.log(error);
        alert(error);
      });
    },
    loginSessionSuccessRedirect (redirect) {
      axios.get("/api/login/session/check", {
        headers: {},
        params: {}
      }).then((response) => {        
        console.log("/api/login/session/check", response.data.success);
        if (response.data.success == true) {
          console.log(response.data.success);
          console.log("redirect", redirect);
          router.push(redirect);          
        } else {
          console.log(response.data.success);
        }
      }).catch((error) => {
        console.log(error);
        alert(error);
      });
    },
  },
  //Vuex  ,  state      
  actions: {
    //
  },
  modules: {
    //
  },
  //vuex getters      state       . 
  getters: {
    getId: function (state) {
      return state.id + '_hello';
    }    
  },
  //vuex store      
  plugins: [
    createPersistedState()
  ],  
});